import * as React from "react";
import { Link } from "gatsby";
import { useEffect, useState } from "react";

const ReactiveLink = ({ name, path }) => {
  const hasWindow = typeof window !== undefined; // this checks if window is defined, which it is in the browser, but not during compile
  const topLvlPath = () =>
    hasWindow ? window.location.pathname.split("/")[1] : ""; // if window is not defined, return empty string
  return (
    <Link className={`navbar-link`} to={`/${path}`}>
      <div
        className={`navbar-link ${
          topLvlPath() === path ? "navbar-selected" : ""
        }`}
      >
        {name}
      </div>
      <div className={`navbar-link-bar`}></div>
    </Link>
  );
};

export default function Navbar(props) {
  const hasWindow = typeof window !== undefined;
  const breakpoint = 80;
  const [scroll, setScroll] = useState("");

  const onScroll = () => {
    const scroll = hasWindow ? window.scrollY : 0;
    if (scroll > breakpoint) setScroll("scrolled");
    else setScroll("");
  };

  useEffect(() => {
    if (hasWindow) {
      window.addEventListener("scroll", onScroll, { passive: true });
      return () => {
        window.removeEventListener("scroll", onScroll);
      };
    }
  }, []);

  return (
    <header
      className={`navbar-outer ${scroll} ${
        props.transparent ? "" : "navbar-background"
      }`}
    >
      <div className={`nav-container ${scroll}`}>
        <div className={`navbar-grid-left ${scroll}`}>
          <Link to={`/`}>
            <img
              className={`logo ${scroll}`}
              src="https://educast.library.gatech.edu/wp-content/uploads/2020/10/cropped-cropped-cropped-logoGearsTechRound100x100-1.png"
            />
          </Link>
        </div>
        <div className="navbar-grid-right">
          <ReactiveLink name="Projects" path="projects" />
          <ReactiveLink name="About" path="about" />
          <ReactiveLink name="Contact" path="contact" />
        </div>
      </div>
      {props.transparent && <div className={`navbar-gradient ${scroll}`}></div>}
    </header>
  );
}
